/* ==|======================
   Layout/Home
   ========================= */
// home promo section START
#home-main{
	background: url(../img/homepage/home-page-promo.jpg) no-repeat;
	min-height: 1200px;
	background-size: cover;
	background-position: center;
	position: relative;
}
#home-promo-area{
	position: relative;
	clear: both;
}
.home-promo-map{
	margin-top: 45px;
	max-width: 100%;
	max-height: 610px;
	height: auto;
}
.home-promo-text{
	font-size: 51px;
	font-weight: 600;
	margin-top: 105px;
	span{
		color: $highlight-text;
	} 
}
.home-promo-youtube-link{
	a{
		color: #000;
		img{
			height: 70px;
			width: 70px; 
			float: left;
		}

		span{
			font-size: 16px;
			font-weight: $semibold-text;
			float: left;
			margin-top: 22px;
			margin-left: 15px;
			text-transform: uppercase;
		}
	}
}
.home-promo-partner{
	h1{
		font-size: 40px;
		font-weight: $semibold-text;
		text-align: center;
		margin-top: 0px;
		margin-bottom: 0px;
	}
	p{
		margin-top: 15px;
		text-align: center; 
	}
	p:nth-child(3){
		margin-top: 30px;

		a{
			color: #000;
			text-decoration: underline;
			font-weight: $semibold-text;
		}
	}
}
.home-promo-footer-img {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    max-width: 100%;
    overflow: hidden;
}
// home promo section START

// affiliate section START
#home-affiliate-area{
	background: url(../img/homepage/affiliate-bg.jpg) no-repeat;
	min-height: 585px;
	background-size: cover;
	background-position: center;
	position: relative;

	.home-section-text{
		h1{
			font-size: 40px;
			text-transform: uppercase;
			font-weight: $semibold-text;
			margin-bottom: 0;
		}
		.home-section-sub-title{
			font-size: 14px;
			font-weight: $regular-text;
			text-transform: uppercase;
		}
		.home-section-details{
			font-size: 18px;
			margin-top: 35px;
		}
	}
	.affiliate-img{
		max-width: 620px;
		position: absolute;
		top: -120px;
		left: -60px;
		height: auto;
	}
}
.home-section-padd {
	padding-top: 145px;
	padding-left: 150px;
	padding-right: 100px;
}
.home-section-inv-padd {
    padding-top: 145px;
    padding-left: 100px;
    padding-right: 150px;
}
.home-center-section-padd{
    padding-left: 150px;
    padding-right: 150px;
    padding-bottom: 50px;
}
// affiliate section END

