// blog section START
#home-blog-area{
	background: url(../img/homepage/affiliate-bg.jpg) no-repeat;
	background-size: cover;
	background-position: center;
	position: relative;
}
.home-blog-post{
	.thumbnail{
		border-radius: 0;
		padding:0;
		.caption{
			padding: 30px 20px;

			.home-blog-date{
				color: #909090;
				font-size: 14px;
			}
			h4{
				font-size: 16px;
				color: #000000;
				margin:10px 0 30px 0; 
			}
			.home-blog-details{
				font-size: 15px;
				color: #909090;
				max-height: 65px;
				overflow: hidden;
				margin-bottom: 20px;
			}
		}
	}
}
// blog section END