}


/* =Wordpress alignment
-------------------*/

.alignleft {
    display: inline;
    float: left;
}
.alignright {
    display: inline;
    float: right;
}
.aligncenter {
    clear: both;
    display: block;
    margin-left: auto;
    margin-right: auto;
}
.txtleft{
  text-align:left;
}
.txtright{
  text-align:right;
}

