.l-footer {
	background: url(../img/homepage/news_footer.jpg) no-repeat;
	background-size: cover;
	background-position: center;
	position: relative;
	min-height: 1100px;

	.footer-env-image{
		height: 50px;
		width: 50px;
		margin-top: 70px;
		margin-bottom: 20px;
	}
	.md-title{
		margin: 0;
	}
	.footer-sub-title{
		font-size: 20px;
		font-weight: 400;
		margin-bottom: 35px;
	}
}
